"use client";

// React and Next
import { useEffect, useState } from "react";
import { useRouter } from "nextjs-toploader/app";

// API
import {
  wishListDeleteApi,
  wishiListApi,
  wishiListApiCount,
} from "@/api/serversideaction/cartapi";

// 3rd Party
// Redux
import { useDispatch, useSelector } from "react-redux";
import { offsetCount } from "@/redux/slices/userSlice";

// Components
import Breadcrumb from "../Breadcrumbs";
import Sidebar from "../Myprofile/sidebar";
import ProductCard from "./ProductCard";
import Contactus from "../Contactus";
import { useTranslation } from "react-i18next";

export default function WishList({ token }) {
  const dispatch = useDispatch();
  const wishCount = useSelector((state) => state.userReducer.wishCount);
  const offset = useSelector((state) => state.userReducer.offsetVal);
  const [wishListCount, setWishListCount] = useState([]);
  const [wishListapicount, setwishListapicount] = useState(0);
  const [trriger, setTrriger] = useState(0);
  const [showLoader, setLoader] = useState(false);
  const { t } = useTranslation();
  const router = useRouter();

  // pagenation

  // const off = searchparams.get("offset") ? searchparams.get("offset") : 0;
  // const cleanedStr =
  //   off && off.replace(/[a-zA-Z]+/g, "") ? off.replace(/[a-zA-Z]+/g, "") : 0;
  // const [offset, setOffset] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);

  // const tri
  const handWishListCount = (data) => {
    setLoader(true);
    setWishListCount(data);
  };

  useEffect(() => {
    if (token !== undefined) {
      wishiListApi(handWishListCount, dispatch, offset);
      wishiListApiCount(setwishListapicount, dispatch);
    } else {
      setLoader(true);
      router.push("/buyer-login");
    }
  }, [token]);

  // pagination api call

  const pagenationapi = async () => {
    wishiListApi(handWishListCount, dispatch, offset);
  };

  useEffect(() => {
    pagenationapi();
  }, [offset]);

  const onPageChange = (data) => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });

    let limit = 5;
    let offset = Math.ceil((data - 1) * limit);
    // setOffset(offset);
    dispatch(offsetCount(offset));
    setCurrentPage(data);
    // router.push(`/wishlist?offset=${offset}`);
  };

  const nPages1 = Math.ceil(
    wishListapicount != undefined && wishListapicount / 5
  );
  useEffect(() => {
    setCurrentPage(offset / 5 + 1);
  }, []);

  const handleClear = (data) => {
    wishListDeleteApi(
      data.productId,
      dispatch,
      wishCount,
      handWishListCount,
      trriger,
      setTrriger
    );
  };
  useEffect(() => {
    if (trriger != 0) {
      wishiListApiCount(setwishListapicount, dispatch);
    }
  }, [trriger]);

  const breadCrumb = [
    {
      text: t("Breadcrumb.home"),
      url: "/",
    },
    {
      text: t("Breadcrumb.accountSettings"),
      url: "/account-settings",
    },
    {
      text: t("Breadcrumb.wishList"),
      url: "javascript:void(0)",
    },
  ];

  const handleAddItemToCart = (datacart) => {
    router.push(`/products/${datacart?.productSlug}`);
  };

  return (
    <>
      <div className="mx-auto px-4 py-2.5 max-w-screen-2xl">
        <Breadcrumb breadCrumb={breadCrumb} />
        <main className="flex min-h-screen mx-auto mt-1 max-w-screen-2xl">
          <Sidebar />
          <ProductCard
            wishListProducts={wishListCount}
            loader={showLoader}
            handleClear={handleClear}
            handleAddItemToCart={handleAddItemToCart}
            wishListapicount={wishListapicount}
            nPages1={nPages1}
            currentPage={currentPage}
            onPageChange={onPageChange}
          />
        </main>
      </div>

      <Contactus token={token} />
    </>
  );
}
