"use client";
// React and Next
import Link from "next/link";
// API
import { imageUrl } from "@/utilities/url";
// Utils
import { PriceCalculate } from "@/utilities/PriceCalculate";
import PaginationComp from "@/Components/Pagination";
//Components
import CustomCurrencyFormatter from "../CustomCurrencyFormatter";
import { useTranslation } from "react-i18next";

export default function ProductCard({
  wishListProducts,
  loader,
  handleClear,
  handleAddItemToCart,
  wishListapicount,
  nPages1,
  currentPage,
  onPageChange,
}) {
  const { t } = useTranslation();
  const skele = [1, 2, 3, 4, 5];
  
  return (
    <div className="w-full px-0 py-6 pr-0 sm:px-8 sm:pr-4">
      <div className="mb-6 flex flex-wrap items-center justify-between gap-2.5 sm:text-start">
        <h5 className="text-black-300 text-lg font-black">
          {t("myprofile.MyWishlist")} (
          {wishListProducts?.length == 0 ? 0 : wishListapicount})
        </h5>
      </div>
      <div>
        {wishListProducts.length >= 1
          ? loader &&
            wishListProducts?.map((data, index) => (
              <div
                key={index}
                className="mb-4 block rounded-[5px] border border-[#D1DADF]"
              >
                <div className="p-4">
                  <div className="flex justify-between gap-2.5">
                    <div className="flex w-full flex-col items-center space-y-4 lg:flex-row lg:space-x-4 lg:space-y-0 lg:rtl:space-x-[unset]">
                      <div className="flex h-[152px] min-w-[152px] items-center rounded-[5px] lg:rtl:ml-4">
                        <img
                          src={
                            imageUrl +
                            "?path=" +
                            data?.containerName +
                            "&name=" +
                            data?.image +
                            "&width=240&height=240"
                          }
                          alt={data?.name}
                          className="mx-w-auto h-full w-full rounded-[5px] object-cover"
                          onError={({ currentTarget }) => {
                            currentTarget.onerror = null;
                            currentTarget.src = "/img/no-image.png";
                            currentTarget.alt = "no-image";
                          }}
                        />
                      </div>
                      <div className="flex w-full flex-col space-y-5 sm:space-y-[46px]">
                        <div className="flex flex-col space-y-4">
                          <Link
                            prefetch={false}
                            href={`/products/${data?.productSlug}`}
                            className="text-black-300 line-clamp-3 text-base font-normal capitalize"
                          >
                            {data?.productNameTrans
                              ? data?.productNameTrans
                              : data?.name}
                          </Link>
                          <p className="text-black-300 mb-0 text-lg font-black">
                            <CustomCurrencyFormatter
                              price={PriceCalculate(
                                data?.price,
                                data?.pricerefer,
                                data?.flag,
                                data?.taxType,
                                data?.taxValue,
                              )}
                            />
                          </p>
                        </div>
                        <div className="flex items-end space-x-2 rtl:space-x-[unset]">
                          <button
                            onClick={() => handleAddItemToCart(data)}
                            className="bg-boldblack btn rtl:space-[unset] flex h-[49px] w-full items-center justify-center space-x-[5px] whitespace-nowrap rounded px-4 text-sm font-semibold text-white hover:bg-zinc-800 sm:w-auto sm:px-[41px] rtl:ml-2"
                          >
                            <img
                              src="/img/shop-cart.svg"
                              alt="shop-cart"
                              className="rtl:ml-[5px]"
                            />
                            <p>{t("productlist.Buynow")}</p>
                          </button>
                          <button
                            onClick={() => handleClear(data)}
                            className="cancel-btn-shadow btn rtl:space-[unset] flex h-[49px] w-full cursor-pointer items-center justify-center space-x-[5px] rounded-[3px] border border-[#191919] px-4 text-sm font-semibold text-[#191919] shadow-sm hover:bg-slate-100 sm:w-auto sm:px-[41px]"
                          >
                            <img
                              src="/img/close-black.svg"
                              alt="close-black"
                              className="rtl:ml-[5px]"
                            />
                            <p>{t("checkout.remove")}</p>
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))
          : loader && (
              <div className="mx-auto mt-32 min-h-[40vh]">
                <div className="flex flex-col items-center justify-center gap-8 p-4 text-center sm:flex-row">
                  <img src="/img/no-data.svg" alt="no-data" />
                  <div>
                    <h5 className="text-black-300 mb-1 text-xl font-black">
                      {t("myprofile.NoData")}
                    </h5>
                  </div>
                </div>
              </div>
            )}

        {loader == false && (
          <div className="border-color rounded border">
            {skele.map(
              (_, index) =>
                loader == false && (
                  <div
                    key={index}
                    role="status"
                    className="border-color flex animate-pulse items-start justify-between border-b px-6 py-4"
                  >
                    <div className="md:flex md:items-center md:space-x-4 md:space-y-0 rtl:space-x-reverse">
                      <div className="flex h-24 w-32 max-w-full items-center justify-center rounded bg-gray-300 dark:bg-gray-700">
                        <svg
                          className="h-10 w-10 text-gray-200 dark:text-gray-600"
                          aria-hidden="true"
                          xmlns="http://www.w3.org/2000/svg"
                          fill="currentColor"
                          viewBox="0 0 20 18"
                        >
                          <path d="M18 0H2a2 2 0 0 0-2 2v14a2 2 0 0 0 2 2h16a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2Zm-5.5 4a1.5 1.5 0 1 1 0 3 1.5 1.5 0 0 1 0-3Zm4.376 10.481A1 1 0 0 1 16 15H4a1 1 0 0 1-.895-1.447l3.5-7A1 1 0 0 1 7.468 6a.965.965 0 0 1 .9.5l2.775 4.757 1.546-1.887a1 1 0 0 1 1.618.1l2.541 4a1 1 0 0 1 .028 1.011Z" />
                        </svg>
                      </div>
                      <div className="w-full">
                        <div className="mb-2.5 h-2 max-w-[440px] rounded-full bg-gray-200 dark:bg-gray-700"></div>
                        <div className="mb-2.5 h-2 max-w-[460px] rounded-full bg-gray-200 dark:bg-gray-700"></div>
                        <div className="h-2 max-w-[360px] rounded-full bg-gray-200 dark:bg-gray-700"></div>

                        <div className="mt-5 h-2 w-52 rounded-full bg-gray-200 dark:bg-gray-700"></div>
                      </div>
                    </div>
                    <div className="h-10 w-32 rounded bg-gray-300 dark:bg-gray-700"></div>
                  </div>
                ),
            )}
          </div>
        )}
      </div>

      <div className="relative mt-0 flex w-full flex-col sm:flex-row">
        {wishListapicount > 5 && (
          <PaginationComp
            nPages1={nPages1}
            currentPage={currentPage}
            onPageChange={onPageChange}
          />
        )}
      </div>
    </div>
  );
}
